import React, { useState } from 'react';
import {Link} from 'react-router-dom'
import { Input, Select, Button } from '@holism/core';

import '../css/Reg.css';

const Reg = () => {

	const [valueName, setValueName] = useState('');
	const [valueSerName, setValueSerName] = useState('');
	const [valueFather, setValueFather] = useState('');
	const [valueEmail, setValueEmail] = useState('');
	const [valuePass, setValuePass] = useState('');
	const [valueRepPass, setValueRepPass] = useState('');

	const optionsData = [
	];

	const optionsSex = [
	  { label: 'Мужской', value: '0' },
	  { label: 'Женский', value: '1' },
	];

	var gettDate = async () =>{
		const api = await fetch('localhost:8000/divisions');
		const data = api.json();
		console.log(data);
		for(var key in data){
			optionsData.push({label: `${key}`, value: `${data[key]}`});
		}
	}

	document.addEventListener("DOMContentLoaded", gettDate);


	return(
		<form className="main_block_reg" action="localhost:8000/registrate">
			<p className="text-center">Регистрация</p>
			<div className="input_reg">
				<Input placeholder="Имя" name="name" value={valueName} onChange={(e, val) => setValueName(val)} />
			</div>
			<div className="input_reg">
				<Input placeholder="Фамилия" name="surname" value={valueSerName} onChange={(e, val) => setValueSerName(val)} />
			</div>
			<div className="input_reg">
				<Input placeholder="Отчество" name="patr" value={valueFather} onChange={(e, val) => setValueFather(val)} />
			</div>
			<div className="select_reg">
				<Select
			      placeholder="Пол"
			      name="sex"
			      dimension="small"
			      withoutOptionMessage="Ничего не найдено"
			      options={optionsSex}
			      selected={items => console.log('selected items', items)}
			      onChange={value => console.log('Value from changed input', value)}
			      onInputChange={value => console.log('onInputChange from changed input', value)}
			      isClearable={false}
			      onInnerMenuOpen={(clientRect: ClientRect) => {console.log(clientRect)}}
			    />
			</div>
			<div className="input_reg">
				<Input type="email" placeholder="E-mail" name="email" value={valueEmail} onChange={(e, val) => setValueEmail(val)} />
			</div>
			<div className="select_reg">
				<Select
			      placeholder="Департамент"
			      name="division"
			      dimension="small"
			      withoutOptionMessage="Ничего не найдено"
			      options={optionsData}
			      selected={items => console.log('selected items', items)}
			      onChange={value => console.log('Value from changed input', value)}
			      onInputChange={value => console.log('onInputChange from changed input', value)}
			      isClearable={false}
			      onInnerMenuOpen={(clientRect: ClientRect) => {console.log(clientRect)}}
			    />
			</div>
			<div className="input_reg">
				<Input type="password" name="password" placeholder="Пароль" value={valuePass} onChange={(e, val) => setValuePass(val)} />
			</div>
			<div className="input_reg">
				<Input type="password" name="passagain" placeholder="Повторите пароль" value={valueRepPass} onChange={(e, val) => setValueRepPass(val)} />
			</div>
			<Button className="button_reg" type="submit" dimension="small">Зарегистрироваться</Button>
			<Link to="/enter" className="text-center">
				Уже зарегистрированы? Войти
			</Link>
		</form>
	);
}

export default Reg;