import React, { useState } from 'react';
import {Link} from 'react-router-dom'
import { Button, Input, Checkbox } from '@holism/core';
import { RefreshIcon } from '@holism/icons';

import '../css/Reg.css';


const Enter = () => {
	const [valueEmail, setValueEmail] = useState('');
	const [valuePass, setValuePass] = useState('');
	const [value1, setValue1] = useState(false);

	return(
		<form className="main_block_reg">
			<p className="text-center">Войти в личный кабинет</p>
			<div className="input_reg">
				<Input placeholder="E-mail" type="email"  value={valueEmail} onChange={(e, val) => setValueEmail(val)} />
			</div>
			<div className="input_reg">
				<Input placeholder="Пароль" type="password"  value={valuePass} onChange={(e, val) => setValuePass(val)} />
			</div>
			<div className="input_reg">
				<Checkbox
			        id="CheckboxID"
			        label="Запомнить меня"
			        checked={value1}
			        onChange={() => {
			          setValue1(flag => !flag);
			        }}
			      />
			</div>
			<Button className="button_reg" type="submit" dimension="small">Войти</Button>
			<Link to="" className="text-center">
				Забыли пароль?
			</Link>
			<Link to="/reg" className="text-center">
				Регистрация
			</Link>
		</form>
	);
}

export default Enter;